import React, {useState} from 'react';
import './App.scss';
import {HashRouter as Router, Route} from 'react-router-dom';

// Pages
import Edit from './pages/Edit';
import Party from './pages/Party';
import Landing from './pages/Landing';
import Admin from './pages/Admin';

// Firebase
import { db } from './services/firebase';

function App() {
  
  return (
    <Router>
      <AppRoutes/>
    </Router>
  );
}

function AppRoutes() {
  const [languageISO, setLanguageISO] = useState('en');
  const [languageActives, setLanguageActives] = useState('en');
  const [languages, setLanguages] = useState({});

  React.useEffect(function RetrieveData() {
    const reflanguageActives = db.ref("language");
    const refLanguages = db.ref("language/isoLangs");

    reflanguageActives.on("value", function (snapshot) {
      setLanguageActives(snapshot.val());
    }, function (errorObject) {
      console.log("The read failed: " + errorObject.code);
    });
    
    refLanguages.on("value", function (snapshot) {
      setLanguages(snapshot.val());
    }, function (errorObject) {
      console.log("The read failed: " + errorObject.code);
    });
  }, []);

  return <>
    <Route exact path="/">
      <Landing
        languageISO={languageISO}
        setLanguageISO={setLanguageISO}
        languageActives={languageActives}
      />
    </Route>
    <Route exact path="/edit"><Edit/></Route>
    <Route exact path="/admin"><Admin/></Route>
    <Route exact path="/party/:id">
      <Party 
        setLanguageISO={setLanguageISO}
        languageISO={languageISO}
        language={languages[languageISO.nativeName]}
      />
    </Route>
  </>;
}

export default App;
