import React, {useState, useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import ShareIcon from '@material-ui/icons/Share';
import HelpIcon from '@material-ui/icons/Help';
import RefreshIcon from '@material-ui/icons/Refresh';
import CachedIcon from '@material-ui/icons/Cached';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Button from '@material-ui/core/Button';
import SettingsIcon from '@material-ui/icons/Settings';
import './Card.scss';
import DialogHelp from './Dialog_help';
import Settings from './Settings';

// Dialog
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import Container from '@material-ui/core/Container'


const useStyles = makeStyles((theme) => ({
    root: {
        // maxWidth: 345,
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    flex: {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center'
    }
}));


function CardComponent(props) {
    const {question, title, help, initialLetter, onUpdateCard} = props;
    const classes = useStyles();

    return (
        <div className="card-component">
            <Card className={classes.root}>
                <CardHeader
                        classes={{title: classes.flex}}
                        title={
                            <>
                                <Settings/>
                                <Typography variant="h5" >
                                    {title}
                                </Typography>
                                <DialogHelp Icon={HelpIcon}>
                                    <Container maxWidth="sm">
                                        {help}
                                    </Container>
                                </DialogHelp>
                            </>
                        }
                />
                <CardContent>
                    <Typography variant="body2" color="textSecondary" component="p">
                        {question}
                    </Typography>
                </CardContent>
                
            </Card>
           
        </div>
    );
}

// Dialog
const useStylesDialog = makeStyles((theme) => ({
    appBar: {
        position: 'relative',
    },
    title: {
        marginLeft: theme.spacing(2),
        flex: 1,
    },
    content: {
        marginTop: '20px'
    }
}));


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

function DialogComponent({message, Icon}) {

    const classes = useStylesDialog();

    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <>
            <IconButton aria-label="help" style={{ color: '#F44336' }} onClick={handleClickOpen}>
                <Icon/>
            </IconButton>
            <Dialog fullScreen open={open} onClose={handleClose} aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description" TransitionComponent={Transition}>
                <AppBar className={classes.appBar} >
                    <Toolbar>
                        <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
                            <CloseIcon />
                        </IconButton>
                        <Typography variant="h6" className={classes.title}>
                            Help
                        </Typography>
                    </Toolbar>
                </AppBar>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        <Container maxWidth="sm" className={classes.content}>
                            {message}
                        </Container>
                    </DialogContentText>
                </DialogContent>
            </Dialog>
        </>
    );
}

export default CardComponent;