import React, {useState, useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import './Party.scss'
import CardComponent from '../components/Card';
import Button from '@material-ui/core/Button';
import getCard from '../services/getCards'
import Header from '../layout/header';
import { randomIdArray } from '../helpers/utils';
import { db, auth } from '../services/firebase';

const useStyles = makeStyles((theme) => ({
    container: {
        display: 'flex',
        marginTop: '1.2rem',
        display: 'flex',
        justifyContent: 'space-between'
    },
    flexSpace: {
        flexBasis: 'calc(33.33% - 0.2rem)'
    }
}));

function Party(props) {
    const {
        language = 'en',
        setLanguageISO
    } = props;

    const classes = useStyles();
    const [card, setCard] = useState(getCard());
    const [question, setQuestion] = useState(0);
    const [questions, setQuestions] = useState([]);
    
    useEffect( function initQuestions(){
        setQuestions(card.questions[language]);
    }, [card]);
    
    useEffect( function initQuestion(){
        setQuestion( randomIdArray(questions) );
    }, [questions]);

    function onUpdateCard() {
        setCard(getCard());
    }

    function handleNextClick() {
        let nextId = question + 1 < questions.length ? question + 1 : 0;
        setQuestion(nextId);
    }

    function handlePrevClick() {
        let prevId = question - 1 > 0 ? question - 1 : questions.length - 1;
        setQuestion(prevId);
    }

    return (
        <div className="party-page">
            <Header></Header>
            <Container maxWidth="xs" className="container">
                <CardComponent 
                    question={questions[question]}
                    questions={card.questions.en}
                    title={card.title.en}
                    initialLetter={card.topic[0].toUpperCase()}
                    help={card.help}
                    onUpdateCard={onUpdateCard}
                />

                <div className={`${classes.container} wrapper-btn`}>
                    <Button variant="contained" size="large" color="primary" className={classes.flexSpace} onClick={handlePrevClick}>
                        Prev
                    </Button>
                    <Button variant="contained" size="large" color="primary" className={classes.flexSpace} onClick={onUpdateCard}>
                        Random
                    </Button>
                    <Button variant="contained" size="large" color="primary" className={classes.flexSpace}  onClick={handleNextClick}>
                        Next
                    </Button>
                </div>
            </Container>
        </div>
    );
}

export default Party;