import React from 'react';
// Styles MUI
import { makeStyles } from '@material-ui/core/styles';

// Components Mui
import AppBar from '@material-ui/core/AppBar';
import Typography from '@material-ui/core/Typography';
import FormControl from '@material-ui/core/FormControl'
import FormGroup from '@material-ui/core/FormGroup'
import FormHelperText from '@material-ui/core/FormHelperText'
import FormLabel from '@material-ui/core/FormLabel'
import Divider from '@material-ui/core/Divider';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';

// Icons Mui
import CloseIcon from '@material-ui/icons/Close';

// Dialog
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import Toolbar from '@material-ui/core/Toolbar';
import Slide from '@material-ui/core/Slide';
import Container from '@material-ui/core/Container'

// Icons
import IconButton from '@material-ui/core/IconButton';
import SettingsIcon from '@material-ui/icons/Settings';


// Dialog
const useStylesDialog = makeStyles((theme) => ({
    appBar: {
        position: 'relative',
    },
    title: {
        marginLeft: theme.spacing(2),
        flex: 1,
    },
    content: {
        marginTop: '20px'
    }
}));


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

function Settings({children, Icon}) {

    const classes = useStylesDialog();

    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <>
            <IconButton aria-label="settings" style={{ color: '#F44336' }} onClick={handleClickOpen}>
                <SettingsIcon/>
            </IconButton>
            <Dialog fullScreen open={open} onClose={handleClose} aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description" TransitionComponent={Transition}>
                <AppBar className={classes.appBar} >
                    <Toolbar>
                        <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
                            <CloseIcon />
                        </IconButton>
                        <Typography variant="h6" className={classes.title}>
                            Settings
                        </Typography>
                    </Toolbar>
                </AppBar>
                <DialogContent className={classes.content}>
                    <ContentSettings 
                        initialDifficultyStates={[
                            true,
                            true,
                            true,
                        ]}

                        difficulties={[
                            'Beginner',
                            'Intermediate',
                            'Advance',
                        ]}

                        initialTopicStates={[
                            false,
                            false,
                            false
                        ]}

                        topics={[
                            'Family',
                            'Hobbies',
                            'HaveEver',
                        ]}
                    />
                        
                </DialogContent>
            </Dialog>
        </>
    );
}

function ContentSettings(props) {
    const {
        initialDifficultyStates,
        difficulties,
        initialTopicStates,
        topics,
    } = props

    const [difficultyStates, setDifficultState] = React.useState([
        ...initialDifficultyStates,
    ]);

    const [allDifficultiesCheck, setAllDifficultiesCheck] = React.useState(false);
    
    const [topicStates, setTopicsState] = React.useState([
        ...initialTopicStates,
    ]);

    const [allTopicsCheck, setAllTopicsCheck] = React.useState(false);

    const handleDifficultyChange = (event) => {
        const newDifficultyStates = [...difficultyStates];
        newDifficultyStates[event.target.name] = event.target.checked;
        setDifficultState( newDifficultyStates );
    };
    
    const handleAllDifficultiesCheck = (event) => {
        if( event.target.checked ) {
            setDifficultState( difficultyStates.map( () => true ) );
        } else {
            setDifficultState( difficultyStates.map( () => false ) );
        }

        setAllDifficultiesCheck( event.target.checked );
    };
    
    const handleTopicChange = (event) => {
        const newTopicsStates = [...topicStates];
        newTopicsStates[event.target.name] = event.target.checked;
        setTopicsState(newTopicsStates);
    };

    const handleAllTopicsCheck = (event) => {
        if( event.target.checked ) {
            setTopicsState( difficultyStates.map( () => true ) );
        } else {
            setTopicsState( difficultyStates.map( () => false ) );
        }

        setAllTopicsCheck( event.target.checked );
    };

    React.useEffect( function verifyCheckboxsDifficulty() {
        if( difficultyStates.filter( v => v ).length === difficultyStates.length ) {
            setAllDifficultiesCheck(true);
        } else {
            setAllDifficultiesCheck(false);
        }
        
        if( topicStates.filter( v => v ).length !== topicStates.length ) {
            setAllTopicsCheck(false);
        } else {
            setAllTopicsCheck(true);
        }

    },[difficultyStates, topicStates]);

    function createCheckboxs(elements, elementStates, handler) {

        return elements.map( (element, index) => (
            <FormControlLabel
                control={<Checkbox checked={elementStates[index]} onChange={handler} name={`${index}`} key={index} />}
                label={element}
            />
        ));
    }
    
    return (
        <>
            <form>
                <Container maxWidth="xs">
                    <FormControl component="fieldset">
                        <FormLabel component="legend">Select difficulty</FormLabel>
                        <FormGroup>
                            <FormControlLabel
                                control={<Checkbox checked={allDifficultiesCheck} onChange={handleAllDifficultiesCheck} />}
                                label={"All"}
                            />
                            {createCheckboxs( difficulties, difficultyStates, handleDifficultyChange )}
                        </FormGroup>
                        <FormHelperText></FormHelperText>
                    </FormControl>
                    <Divider light />
                    <FormControl component="fieldset">
                        <FormLabel component="legend">Select topics</FormLabel>
                        <FormGroup>
                            <FormControlLabel
                                control={<Checkbox checked={allTopicsCheck} onChange={handleAllTopicsCheck} />}
                                label={"All"}
                            />
                            {createCheckboxs( topics, topicStates, handleTopicChange )}
                        </FormGroup>
                        <FormHelperText></FormHelperText>
                    </FormControl>
                </Container>
            </form>
        </>
    );
}

export default Settings;